<template>
	<div id="Dheader">
    <div id="Dselect">
      <span id="Dname">{{ name }}</span>
      <el-select v-model="time" placeholder="请选择" size="mini">
        <el-option
          v-for="item in timeselect"
          :key="item.value"
          :label="item.label"
          :value="item.value">
        </el-option>
      </el-select>
    </div>
    <div id="Dimg">
      <el-dropdown trigger="click" @command="handleCommand">
      <a><img src="/icon_add.5170bbfb.png" id="icon_add"></a>
      <el-dropdown-menu slot="dropdown">
        <!-- <el-dropdown-item>Mark Visit</el-dropdown-item> -->
        <el-dropdown-item command="sa">Set Alerts</el-dropdown-item>
        <el-dropdown-item command="as">Add Suggestion</el-dropdown-item>
        <!-- <el-dropdown-item>Archive Client</el-dropdown-item> -->
      </el-dropdown-menu>
    </el-dropdown>
        <!-- <el-dropdown trigger="click">
      <a><img src="@/assets/icon/icon_set.png" id="icon_set"></a>
      <el-dropdown-menu slot="dropdown">
        <el-dropdown-item>Client Details</el-dropdown-item>
        <el-dropdown-item>Mark Visit</el-dropdown-item>
        <el-dropdown-item>Set Alerts</el-dropdown-item>
        <el-dropdown-item>Archive Client</el-dropdown-item>
      </el-dropdown-menu>
    </el-dropdown>
 -->    
    </div>
    <el-dialog
    title="SET ALERTS"
    :visible.sync="alertsVisible"
    width="30%"
    >
      <span>Alert me if <b>{{name}}</b> has not made an entry for </span>
      <el-select v-model="day" placeholder=" " size="mini">
        <el-option
          v-for="item in dayselect"
          :key="item.value"
          :label="item.label"
          :value="item.value">
        </el-option>
      </el-select>
      <span> days</span>
      <span slot="footer" class="dialog-footer">
        <el-button @click="alertsVisible = false" size="small">CANCEL</el-button>
        <el-button type="primary" @click="alertsVisible = false" size="small">DONE</el-button>
      </span>
    </el-dialog>
	</div>
</template>

<script>
  import axios from "axios"
export default {
  data() {
    return {
      path: this.$route.path,
      name: this.$store.state.patient_name,
      alertsVisible: false,
      Cname: '',
      timeselect: [{
        value: 'option1',
        label: 'Since Last Vist'
      }, {
        value: 'option2',
        label: 'All Time'
      }],
      time: 'option1',
      dayselect: [{
        value: '3',
        label: '3'
      }, {
        value: '5',
        label: '5'
      }, {
        value: '7',
        label: '7'
      }, {
        value: '10',
        label: '10'
      }],
      day: '',
    }
   },
   computed: {
    patient_id() {
        return this.$store.state.patient_id
    }
   },
   methods: {
    handleCommand(command) {
      switch (command) {
        case "sa":
          this.alertsVisible = true;
          break;
        case "as":
          this.add_suggestion();
          break;
      }
    },
    add_suggestion() {
        this.$prompt('ADD SUGGESTION', {
          confirmButtonText: 'DONE',
          cancelButtonText: 'CANCEL',
        }).then(({ value }) => {
          axios.get('http://35.197.160.94:8094/doctor/addSuggestion', {
            params: {
              uid_doctor: this.$store.state.user,
              uid_Patient: this.$store.state.patient_id,
              Suggestion: value,
              type: 1,
            }
          })
          .then((response) => {
            if (response.data == "1") {
              this.$message({
              type: 'success',
              message: 'Add Suggestion Successed'
              });
              location.reload(0);
            }
          })
        }).catch((error) => {
          console.log(error)
          this.$message({
            type: 'info',
            message: 'Input Canceled'
          });       
        });
      }
   },
   created() {
     
    },
   watch: {
    patient_id(oldval, newval){
        console.log(oldval);
        this.name = this.$store.state.patient_name
    }
   },
  }
</script>

